import React, { useState } from 'react';
import { motion } from 'framer-motion';

const Projects = () => {
  const [selectedCategory, setSelectedCategory] = useState('all');
  
  const projects = [
    {
      title: "MyFoodIsGettingCold - Movie Recommender",
      description: "High-accuracy (90%+) movie recommendation system using k-Nearest Neighbors, deployed with a custom backend on Raspberry Pi.",
      category: "machine-learning",
      technologies: ["Python", "Scikit-learn", "Flask", "Raspberry Pi"],
      link: "https://shubhammer7.github.io/myfoodisgettingcold/"
    },
    {
      title: "CalcScriber - Handwritten Math Solver",
      description: "Trained a CNN model on 300,000+ images to recognize handwritten numbers and operators, deployed as an interactive web app with real-time prediction.",
      category: "machine-learning",
      technologies: ["Python", "TensorFlow", "Flask", "Raspberry Pi"],
      link: "https://shubhammer7.github.io/CalcScriber/"
    },
    {
    title: "Bitcoin News Sentiment Analysis",
    description: "Analyzed sentiment trends across 10,000+ Bitcoin-related articles using NLP, uncovering market sentiment correlations with Python and NLTK.",
    category: "nlp",
    technologies: ["Python", "NLTK", "TextBlob", "Pandas", "Matplotlib"],
    link: "https://github.com/Shubhammer7/Bitcoin-NewsArticles-Sentiment-Analysis/tree/main"
    }
  ];

  const categories = [
    { id: 'all', name: 'All Projects' },
    { id: 'machine-learning', name: 'Machine Learning' },
    { id: 'data-analysis', name: 'Data Analysis' },
    { id: 'nlp', name: 'NLP' },
    { id: 'dashboards', name: 'Dashboards' }
  ];

  const filteredProjects = selectedCategory === 'all' 
    ? projects 
    : projects.filter(project => project.category === selectedCategory);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: { 
      opacity: 1,
      transition: { staggerChildren: 0.1 }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: { y: 0, opacity: 1, transition: { duration: 0.5 } }
  };

  return (
    <motion.div 
      className="page-container projects-container"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="content-card">
        <motion.h1 
          initial={{ y: -20, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          Projects
        </motion.h1>
        
        <motion.div 
          className="category-filter"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.2, duration: 0.5 }}
        >
          {categories.map(category => (
            <button
              key={category.id}
              className={`category-btn ${selectedCategory === category.id ? 'active' : ''}`}
              onClick={() => setSelectedCategory(category.id)}
            >
              {category.name}
            </button>
          ))}
        </motion.div>
        
        <motion.div 
          className="projects-grid"
          variants={containerVariants}
          initial="hidden"
          animate="visible"
        >
          {filteredProjects.map((project, index) => (
            <motion.div 
              key={index} 
              className="project-card-wrapper"
              variants={itemVariants}
            >
              <a 
                href={project.link} 
                target="_blank" 
                rel="noopener noreferrer"
                className="project-link"
              >
                <div className="project-card">
                  <div className="project-image">
                    <div className="project-icon">
                      {project.title.charAt(0).toUpperCase()}
                    </div>
                    {project.deployed && (
                      <div className="live-badge">
                        <i className="fas fa-circle"></i> Live
                      </div>
                    )}
                  </div>
                  <div className="project-content">
                    <h3>{project.title}</h3>
                    <p>{project.description}</p>
                    <div className="project-tech">
                      {project.technologies.map((tech, idx) => (
                        <span key={idx} className="tech-badge">{tech}</span>
                      ))}
                    </div>
                    <div className="project-link-indicator">
                      <i className="fas fa-external-link-alt"></i> View Project
                    </div>
                  </div>
                </div>
              </a>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </motion.div>
  );
};

export default Projects;